<template>
	<view class="goods-detail">
		<!-- 轮播图区域 -->
		<swiper indicator-dots autoplay :interval="3000" :duration="1000">
			<swiper-item v-for="(item,index) in swipers" :key="index">
				<image :src="item.src"></image>
			</swiper-item>
		</swiper>
		<!-- 价格标题区域 -->
		<view class="info-top">
			<text class="new">￥{{info.sell_price}}</text>
			<text class="old">￥{{info.market_price}}</text>
			<view class="title">
				{{info.title}}
			</view>
		</view>
		<!-- 货号库存信息区域 -->
		<view class="info-bottom">
			<view class="bottom1">货号: {{info.goods_no}}</view>
			<view class="bottom2">库存: {{info.stock_quantity}}</view>
		</view>
		<!-- 详细介绍区域 -->
		<view class="desc">
			<view class="tit">
				详细介绍
			</view>
			<view class="desc-content">
				<rich-text :nodes="desc.content"></rich-text>
			</view>
		</view>
	</view>
</template>

<script>
	import { myRequest } from '../../js/ajax.js'
	export default {
		data() {
			return {
				id: 0,
				swipers: [],
				info: {},
				desc: {}
			}
		},
		methods: {
			async getSwipers () {
				const res = await myRequest({
					url: '/api/getthumimages/'+this.id
				})
				this.swipers = res.data.message
			},
			async getInfo () {
				const res = await myRequest({
					url: '/api/goods/getinfo/'+this.id
				})
				this.info = res.data.message[0]
			},
			async getDesc () {
				const res = await myRequest({
					url: '/api/goods/getdesc/'+this.id
				})
				this.desc = res.data.message[0]
			}
		},
		onLoad (options) {
			this.id = options.id
			this.getSwipers()
			this.getInfo()
			this.getDesc()
		}
	}
</script>

<style lang="scss">
	.goods-detail{
		line-height: 80rpx;
		swiper{
			height: 700rpx;
			image{
				width: 100%;
				height: 100%;
			}
		}
		.info-top{
			.title{
				margin-left: 25rpx;
				font-weight: bold;
			}
			.new{
				font-size: 40rpx;
				color: #FF3030;
				font-weight: bolder;
				margin-left: 30rpx;
			}
			.old{
				font-size: 25rpx;
				color: #808080;
				text-decoration: line-through;
				margin-left: 30rpx;
			}
		}
		.info-bottom{
			border-top: 6rpx solid #999999;
			border-bottom: 6rpx solid #999999;
			margin: 15rpx 0;
			.bottom1{
				margin-left: 25rpx;
			}
			.bottom2{
				margin-left: 25rpx;
			}
		}
		.desc{
			padding: 10rpx;
			width: 750rpx;
			.tit{
				font-size: 40rpx;
				margin-left: 25rpx;
			}
		}
	}
</style>
